
html{height:100%
}
body{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;height:100%
}
#app{color:#2c3e50;max-width:600px;font-family:Source Sans Pro, Helvetica, sans-serif;text-align:center
}
#app a{color:#42b983;text-decoration:none
}
.logo{width:100px;height:100px
}
#main{position:absolute;top:50px;width:100%
}
